.gallery {
  flex-wrap: wrap;
  gap: 30px;
  display: flex;
}

.container {
  width: 1200px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

.search-form {
  width: 100%;
  justify-content: center;
  align-items: center;
  gap: 1em;
  padding: 20px 0;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  box-shadow: 0 3px 5px rgba(0, 0, 0, .5);
}

.photo-card {
  width: auto;
  background-color: rgba(157, 193, 204, .5);
  overflow: hidden;
  box-shadow: 0 0 5px 2px rgba(0, 4, 56, .5);
}

.photo-card img {
  height: 20em;
  width: 25em;
}

.info {
  flex-wrap: wrap;
  gap: 10px;
  display: flex;
}

.photo-card:hover, .photo-card:focus {
  box-shadow: 0 0 10px 5px rgba(255, 255, 255, .5);
}

.photo-img {
  height: 200px;
  width: 100%;
  object-fit: cover;
  overflow: hidden;
}

.search-form input {
  width: 300px;
  height: auto;
  border-radius: 5px 8px;
  outline: none;
  font-size: 16px;
  line-height: 2em;
}

.search-form input::-ms-input-placeholder {
  color: #ccc;
  font-size: 16px;
}

.search-form input::placeholder {
  color: #ccc;
  font-size: 16px;
}

.search-form button {
  width: 150px;
  height: auto;
  text-transform: capitalize;
  cursor: pointer;
  color: #000;
  background-color: #9dc1cc;
  border-radius: 8px;
  font-size: 16px;
  font-weight: 500;
  line-height: 2em;
}

.load-more {
  width: 150px;
  height: auto;
  text-transform: capitalize;
  cursor: pointer;
  color: #000;
  background-color: #9dc1cc;
  border-radius: 8px;
  margin-top: 50px;
  margin-bottom: 30px;
  margin-left: 50%;
  font-size: 16px;
  font-weight: 500;
  line-height: 2em;
  transform: translateX(-50%);
}

.load-more:hover, .load-more:focus {
  color: #000;
  background-color: #ffff7e;
}

.load-more:disabled {
  color: #666;
  background-color: #ccc;
  border: 1px solid #999;
  font-size: 14px;
}

.hidden {
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  position: absolute;
}

